export default function CommentList({ comments }){
    
    if(comments){
        return (
            <>
                <h3>Comments:</h3>
                {comments.map((comment, key) => (
                <div key={key}>
                    <h3>{comment.username}</h3>
                    <p>{comment.user}</p>
                </div>
                
                ))}
            </>
            
        )} else {
        return <h2>No comments yet</h2>
    }

}    